import { Box, Grid, GridItem, Heading ,Image } from "@chakra-ui/react";

function Skills(){
    return (
        <Box h={"80vh"} w="100%" id="skills">
            <Heading pt={"5vh"} textDecoration={"underline"}  fontWeight="semibold">My Skills</Heading>
            <Grid gridTemplateColumns="repeat(3,1fr)" gap={17} m="7vh 17vw" width="70vw" >
                <GridItem cursor={"pointer"} >
                    <Image 
                     boxSize='200px'
                     src='data:image/png;base64,'
                     alt='Dan Abramov'
                     cursor={"pointer"}
                     _hover={{boxSize:"170px" , boxShadow: "rgba(100, 100, 111, 0.2) 0px 7px 29px 0px"}}
                    />
                </GridItem>
                <GridItem >
                    <Image 
                     boxSize='200px'
                     src='data:image/png;base64,'
                     alt='Dan Abramov'
                     cursor={"pointer"}
                     _hover={{boxSize:"170px" , boxShadow: "rgba(100, 100, 111, 0.2) 0px 7px 29px 0px"}}
                    />
                </GridItem>
                <GridItem >
                    <Image 
                        boxSize='200px'
                        src='data:image/png;base64,'
                        alt='Dan Abramov'
                        cursor={"pointer"}
                        _hover={{boxSize:"170px" , boxShadow: "rgba(100, 100, 111, 0.2) 0px 7px 29px 0px"}}
                    />
                </GridItem>
                <GridItem cursor={"pointer"}>
                <Image 
                     boxSize='200px'
                     src='data:image/png;base64,'
                     alt='Dan Abramov'
                     cursor={"pointer"}
                     _hover={{boxSize:"170px" , boxShadow: "rgba(100, 100, 111, 0.2) 0px 7px 29px 0px"}}
                    />
                </GridItem>
                <GridItem >
                <Image 
                     boxSize='200px'
                     src='data:image/png;base64,'
                     alt='Dan Abramov'
                     cursor={"pointer"}
                     _hover={{boxSize:"170px" , boxShadow: "rgba(100, 100, 111, 0.2) 0px 7px 29px 0px"}}
                    />
                </GridItem>
                <GridItem >
                <Image 
                     boxSize='200px'
                     src='data:image/png;base64,'
                     alt='Dan Abramov'
                     cursor={"pointer"}
                     _hover={{boxSize:"170px" , boxShadow: "rgba(100, 100, 111, 0.2) 0px 7px 29px 0px"}}
                    />
                </GridItem>
                
            </Grid>
        </Box>
    )
}

export default Skills